import { observer } from "mobx-react-lite";
import Drawer from '@mui/material/Drawer';
import Typography from "@mui/material/Typography";
import IconButton from "@mui/material/IconButton";
import CancelIcon from "@mui/icons-material/Cancel";
import AddIcon from "@mui/icons-material/Add";
import Button from "@mui/material/Button";
import Box from "@mui/material/Box";
import { store } from "store";
import { useDrinks } from "./use-drinks";
import { useFood } from "./use-food";

export const ProductDrawer = observer(() => {
  const isVolumeNotSelected = store.selectedCategory?.type === "drinks" && !store.order.newProduct?.selectedVolume;
  const drinks = useDrinks(isVolumeNotSelected)
  const food = useFood();

  const body: Record<Category["type"], JSX.Element> = {
    "drinks": drinks,
    "food": food,
  };

  return (
    <Drawer
      anchor="bottom"
      open={!!store.order.newProduct}
      className="drawer"
      sx={{ overflow: "hidden" }}
      onClose={store.handleCloseProductDrawer}
    >
      <Box sx={{ padding: 1.5, position: "relative" }}>
        <Box sx={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginBottom: 2 }}>
          <Typography id="modal-modal-title" variant="h6" component="h2">
             {store.order.newProduct?.displayName.toLowerCase()}
          </Typography>
          <IconButton color="default" aria-label="" onClick={store.handleCloseProductDrawer}>
            <CancelIcon />
          </IconButton>
        </Box>
        <Box sx={{ display: "flex", flexDirection: "column", gap: 2, overflow: "auto", marginBottom: "16px" }}>
          {body[store.selectedCategory?.type ?? "drinks"]}
        </Box>
        <Button
          disabled={isVolumeNotSelected}
          sx={{ width: "100%" }}
          size="medium"
          variant="contained"
          endIcon={!isVolumeNotSelected && <AddIcon />}
          onClick={store.order.addCurrentProduct}
        >
          {isVolumeNotSelected ? " " : `${store.order.newProductPrice}`}
        </Button>
      </Box>
    </Drawer>
  )
});