export const useList = () => {
  return store.source?.categories.map((category, index) => (
    <CustomTabPanel key={`${category.id}_${index}`} value={store.activeTabId} index={index}>
      <List sx={{ width: '100%', bgcolor: 'background.paper', overflow: "auto" }}>
        {category.products.map((product, index) => {
          const price = product.prices?.["0,2"] ?? product.prices?.["0,3"] ?? product.prices?.["0,4"] ?? product.prices?.["0,6"] ?? product.price ?? "";
          const icon = category.type === "food" ? <FastfoodIcon /> : <CoffeeIcon />
          const productCaption = `${category.type === "food" ? "" : " "}${price}`;

          return (
            <ListItem disablePadding key={`${product.id}_${index}`} sx={{ ":hover": { cursor: "pointer" } }}>
              <ListItemButton onClick={store.handleProductClick(product.id)}>
                <ListItemAvatar>
                  <Avatar>
                    {icon}
                  </Avatar>
                </ListItemAvatar>
                <ListItemText primary={product.displayName} secondary={productCaption} />
              </ListItemButton>
            </ListItem>
          )
        })}
        <ListItem disablePadding key="invisible-1" sx={{ ":hover": { cursor: "default" } }}>
          <ListItemButton>
            <ListItemText primary="" secondary="" />
          </ListItemButton>
        </ListItem>
        <ListItem disablePadding key="invisible-2" sx={{ ":hover": { cursor: "default" } }}>
          <ListItemButton>
            <ListItemText primary="" secondary="" />
          </ListItemButton>
        </ListItem>
        <ListItem disablePadding key="invisible-3" sx={{ ":hover": { cursor: "default" } }}>
          <ListItemButton>
            <ListItemText primary="" secondary="" />
          </ListItemButton>
        </ListItem>
      </List>
    </CustomTabPanel>
  ))
}
