import { Fragment } from "react";
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import RadioGroup from "@mui/material/RadioGroup";
import Radio from "@mui/material/Radio";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import Box from "@mui/material/Box";
import { store } from "store";
import { ProductCount } from "../product-count";

export const useDrinks = (isVolumeNotSelected: boolean) => {
  const volumes = (
    <Fragment>
      <Typography sx={{ fontSize: "16px", lineSize: "16px" }} id="modal-modal-title" variant="h6" component="h6">
        
      </Typography>
      <Box sx={{ display: "flex", gap: 1, }}>
        {Object.keys(store.order.newProduct?.prices ?? {}).map((priceKey) => {
          return (
            <Box sx={{ display: "flex", flexDirection: "column", gap: 0.5 }}>
              <Button variant={store.order.newProduct?.selectedVolume === priceKey ? "contained" : "outlined"} color="info" size="small" onClick={store.handleVolumeClick(priceKey as ProductVolume)}>{priceKey}</Button>
              <Typography sx={{ textAlign: "center", fontWeight: "bold" }} id="modal-modal-title" variant="caption" color="gray">
                {store.order.newProduct?.prices?.[priceKey as ProductVolume]?.toString()}
              </Typography>
            </Box>
          )
        })}
      </Box>
    </Fragment>
  )

  return (
    <Fragment>
      {volumes}
      {store.source?.additional.map((additionalCategory) => {
        if (additionalCategory.type === "food") return null;

        const isMultiSelect = typeof additionalCategory.multiSelect === "boolean" && !additionalCategory.multiSelect;

        let products: any = additionalCategory.products.map((product) => {
          let productItem = (
            <FormControlLabel disabled={isVolumeNotSelected} control={<Checkbox size="small" />} label={
              <Typography sx={{ textAlign: "center" }} id="modal-modal-title" variant="caption">
                {product.displayName}
              </Typography>
            } />
          );

          if (isMultiSelect) {
            productItem = (
              <FormControlLabel disabled={isVolumeNotSelected} value={product.id} control={<Radio value={product.id} size="small" />} label={
                <Typography sx={{ textAlign: "center" }} id="modal-modal-title" variant="caption">
                  {product.displayName}
                </Typography>
              } />
            );
          }

          if (additionalCategory.isNumberCount) {
            productItem = (
              <Box display="flex" width="100%" alignItems="center" justifyContent="space-between">
                <Typography sx={{ textAlign: "center" }} id="modal-modal-title" variant="body2">
                  {product.displayName}
                </Typography>
                <Box>
                  {!!store.order.newProduct?.sirups?.[product.id]?.count ? (
                    <ProductCount
                      value={store.order.newProduct.sirups?.[product.id]?.count ?? 0}
                      onRemoveClick={store.handleSirupChange(product.id, false)}
                      onAddClick={store.handleSirupChange(product.id, true)}
                    />
                  ) : (
                    <Button sx={{ fontSize: "11px" }} disabled={isVolumeNotSelected} variant="text" color="info" size="small" onClick={store.handleSirupChange(product.id, true)}></Button>
                  )}
                </Box>
              </Box>
            )
          }

          return (
            <ListItem disablePadding>
              {productItem}
            </ListItem>
          )
        })

        if (isMultiSelect) {
          products = (
            <RadioGroup
              aria-labelledby="milk-selector"
              name="milk-selector"
              value={store.order.newProduct?.selectedMilk ?? additionalCategory.products[0].id}
              onChange={store.handleMilkRadioChange}
            >
              {products}
            </RadioGroup>
          );
        }

        return (
          <Box>
            <Typography sx={{ fontSize: "16px", lineSize: "16px" }} variant="h6" component="h6">
              {additionalCategory.displayName}
            </Typography>
            <List sx={{ display: !!additionalCategory.renderingDirection ? "flex" : undefined, flexDirection: additionalCategory.renderingDirection, width: '100%', overflow: "auto", height: additionalCategory.height }}>
              {products}
            </List>
          </Box>
        )
      })}
    </Fragment>
  );
}