import { v4 } from "uuid";
import { makeAutoObservable } from "mobx";
import { ChangeEvent } from "react";
import { telegram } from "telegram";
import { Order } from "./order";
import { addresses } from "./store.consts";
import { Address } from "./store.types";

export class Store {
  public source: View | null = null;
  public selectedAddress: Address | null = null;
  public activeTabId: number = 0;
  public addresses = addresses;
  public isBasketVisible = false;
  public order: Order;

  public get selectedCategory() {
    return this.source?.categories[this.activeTabId];
  }

  constructor() {
    makeAutoObservable(this);

    this.order = new Order(this);
  }

  public getCategoryById = (id: string) => {
    return this.source?.categories.find((category) => category.id === id);
  };

  public getAdditionalById = (id: string) => {
    return this.source?.additional.find((additional) => additional.id === id);
  };

  public handleVolumeClick = (volume: ProductVolume) => () => {
    this.order.setSelectedVolume(volume);
  };

  public handleSirupChange = (sirupId: string, value: boolean) => () => {
    this.order.setSirup(sirupId, value);
  };

  public getMilkProductById = (milkId: string) => {
    const sirupsCategory = this.getAdditionalById("milk");

    if (!sirupsCategory) return;

    return sirupsCategory.products.find((sirup) => sirup.id === milkId);
  };

  public getSirupProductById = (sirupId: string) => {
    const sirupsCategory = this.getAdditionalById("sirups");

    if (!sirupsCategory) return;

    return sirupsCategory.products.find((sirup) => sirup.id === sirupId);
  };

  public handleProductClick = (productId: string) => () => {
    const category = this.source?.categories[this.activeTabId];
    const productSource = category?.products.find(
      (product) => product.id === productId
    );

    if (!productSource) return;

    this.order.newProduct = {
      ...productSource,
      selectedMilk: category?.type === "drinks" ? "default" : undefined,
      orderProductId: v4(),
      count: 1,
      sugarCount: 0,
      isCinnamon: false,
    };
  };

  // public handle

  public handleMilkRadioChange = (event: ChangeEvent<HTMLInputElement>) => {
    this.order.setSelectedMilk(event.target.value);
  };

  public handleCloseProductDrawer = () => {
    this.order.newProduct = null;
  };

  public handleOpenBasket = () => {
    this.isBasketVisible = true;
  };

  public handleCloseBasket = () => {
    this.isBasketVisible = false;
  };

  public handleChangeAddress = (event: ChangeEvent<HTMLInputElement>) => {
    this.selectedAddress = event.target.value as Address;
  };

  public init = async () => {
    console.log("start");
    const source = await fetch("https://vangog-it.ru/source")
      .then((res) => res.json())
      .catch(() => {});

    console.log(source);

    this.source = source;
    telegram?.expand?.();
  };
}

export const store = new Store();
store.init();
