import { observer } from "mobx-react-lite";
import Box from "@mui/material/Box";
import { default as MuiList } from "@mui/material/List";
import ListItem from "@mui/material/ListItem";
import ListItemText from "@mui/material/ListItemText";
import CloseIcon from "@mui/icons-material/Close";
import { store } from "store";

const separator = "  ";

export const List = observer(() => {
  return (
    <MuiList sx={{ width: '100%', overflow: "auto" }}>
      {store.order.products.map((orderProduct, index) => {
        const primaryText = (
          <Box display="flex" alignItems="center" gap="5px" sx={{ "svg": { color: "#00000", width: 14 } }}>
            {`${orderProduct.displayName} ${orderProduct.selectedVolume}`}<CloseIcon />{orderProduct.count} = {store.order.getProductPrice(orderProduct, true)}
          </Box>
        );

        const milk = store.getMilkProductById(orderProduct.selectedMilk ?? "");
        const milkTitle = milk?.displayName ? `${milk.displayName}${separator}` : "";
        const sugarTitle = orderProduct.sugarCount ? ` ${orderProduct.sugarCount} ${separator}` : `  ${separator}`
        const innamonTitle = orderProduct.isCinnamon ? `  ${separator}` : `  ${separator}`

        const productDetails = milkTitle + sugarTitle + innamonTitle + Object.keys(orderProduct.sirups ?? {}).reduce((details, key, index, arr) => {
          const count = orderProduct.sirups?.[key]?.count;

          if (!count) return details;

          const sirupProduct = store.getSirupProductById(key);

          if (!sirupProduct) return details;

          const pattern = `${sirupProduct.displayName} ${count}`;

          if (index >= arr.length - 1) {
            return details + `${pattern}`;
          }

          return details + ` ${pattern}${separator}`;
        }, "");

        return (
          <ListItem disablePadding key={`order-details_${orderProduct.id}_${index}`} sx={{ ":hover": { cursor: "pointer" } }}>
            <ListItemText primary={primaryText} secondary={productDetails} />
          </ListItem>
        )
      })}
    </MuiList>
  );
})