import { observer } from "mobx-react-lite";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import { store } from "store";

export const OrderButton = observer(() => {
  if (store.isBasketVisible || !store.order.products.length) return null;

  return (
    <Box sx={{ position: "fixed", left: 0, bottom: 16, width: "100%", display: "flex", justifyContent: "center", alignItems: "center" }}>
      <Button
        sx={{ width: "95%" }}
        size="medium"
        variant="contained"
        onClick={store.handleOpenBasket}
      >
        {`  ${store.order.fullPrice}`}
      </Button>
    </Box>
  );
});