import { observer } from "mobx-react-lite";
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import { store } from "store";
import { a11yProps } from "./categories.utils";

export const Categories = observer(() => {
  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    store.activeTabId = newValue;
  };


  return (
    <Box sx={{ padding: 0, borderBottom: 1, borderColor: 'divider' }}>
      <Tabs scrollButtons="auto" variant="scrollable" value={store.activeTabId} onChange={handleChange} aria-label="">
        {store.source?.categories.map((category, index) => <Tab key={index} label={category.displayName} {...a11yProps(index)} />)}
      </Tabs>
    </Box>
  )
});
