import { v4 } from "uuid";
import { makeAutoObservable } from "mobx";
import { telegram } from "telegram";
import { Store } from "./store";

export class Order {
  public products: OrderProduct[] = [];
  public newProduct: OrderProduct | null = null;

  constructor(private readonly root: Store) {
    makeAutoObservable(this);
  }

  public get fullPrice() {
    return this.products.reduce((price, product) => {
      return price + this.getProductPrice(product);
    }, 0);
  }

  public get newProductPrice() {
    if (!this.newProduct) return 0;

    return this.getProductPrice(this.newProduct);
  }

  public addCurrentProduct = () => {
    if (!this.newProduct) return;

    this.products.push(this.newProduct);
    this.newProduct = null;
  };

  public setSirup = (sirupId: string, value: boolean) => {
    if (!this.newProduct) {
      return;
    }

    if (!this.newProduct.sirups) {
      this.newProduct.sirups = {};
    }

    if (!this.newProduct.sirups?.[sirupId] && value) {
      this.newProduct.sirups[sirupId] = { count: 1 };

      return;
    }

    if (!value && this.newProduct.sirups[sirupId]?.count === 1) {
      this.newProduct.sirups[sirupId] = undefined;
      delete this.newProduct.sirups[sirupId];
      return;
    }

    const sirup = this.newProduct?.sirups?.[sirupId];

    if (sirup) {
      if (!value) {
        sirup.count -= 1;

        return;
      }

      sirup.count += 1;
    }
  };

  public getProductPrice(product: OrderProduct, byCount = false) {
    const category = this.root.getCategoryById(product.categoryId);

    if (category?.type === "food") {
      return product.price ?? 0;
    }

    if (!product.selectedVolume) return 0;

    const basePrice = product.prices?.[product.selectedVolume] ?? 0;
    const milkCategory = this.root.getAdditionalById("milk");
    const sirupsCategory = this.root.getAdditionalById("sirups");
    const selectedMilkProduct = milkCategory?.products.find(
      (_product) => _product.id === product.selectedMilk
    );

    if (!selectedMilkProduct?.prices) return basePrice;

    let sirupsPrice = 0;
    const sirupsKeys = Object.keys(product.sirups ?? {});

    if (sirupsKeys.length) {
      sirupsPrice = sirupsKeys.reduce((price, key) => {
        const sirupPrice =
          sirupsCategory?.products.find(
            (sirupProduct) => sirupProduct.id === key
          )?.price ?? 0;
        const sirupCount = product.sirups?.[key]?.count ?? 0;

        return price + sirupPrice * sirupCount;
      }, sirupsPrice);
    }

    const fullPrice =
      basePrice +
      sirupsPrice +
      (selectedMilkProduct.prices[product.selectedVolume] ?? 0);

    if (byCount) {
      return fullPrice * (product.count ?? 1);
    }

    return fullPrice;
  }

  public setSelectedMilk = (milk: string) => {
    if (!this.newProduct) return;

    this.newProduct.selectedMilk = milk;
  };

  public setSelectedVolume = (volume: ProductVolume) => {
    if (!this.newProduct) return;

    this.newProduct.selectedVolume = volume;
  };

  public addProduct = (product: OrderProduct) => {};

  public plusProduct = (product: OrderProduct) => {};

  public minusProduct = (product: OrderProduct) => {};

  public toJson = (): OrderDto => {
    return {
      id: v4(),
      products: this.products.map((product) => ({
        ...product,
        fullPrice: this.getProductPrice(product),
      })),
      isPaid: false,
      dueDate: "",
      fullPrice: this.fullPrice,
      telegramInfo: {
        chatId: telegram.initDataUnsafe.chat?.id ?? -1,
        user: {
          id: telegram.initDataUnsafe.user?.id ?? -1,
          username: telegram.initDataUnsafe.user?.username ?? "",
          firstName: telegram.initDataUnsafe.user?.first_name ?? "",
          lastName: telegram.initDataUnsafe.user?.last_name ?? "",
        },
      },
      address: this.root.selectedAddress
        ? this.root.addresses[this.root.selectedAddress].title
        : " ",
    };
  };

  public proceed = () => {
    let json = "";

    try {
      json = JSON.stringify(this.toJson());
    } catch {}

    if (!json.length) return;

    fetch("https://vangog-it.ru/proceed-order", {
      method: "POST",
      body: json,
      headers: {
        "content-type": "application/json",
      },
    })
      .then(async (res) => {
        if (res.status !== 200) {
          return telegram.showAlert(res.status.toString(), () => {
            telegram.close();
          });
        }

        telegram.close();
      })
      .catch((e: Error) => {
        telegram.showAlert(e.message);
      });
  };

  public clear = () => {};
}
