import { memo } from "react";
import { Box, IconButton, Typography } from "@mui/material";
import RemoveIcon from "@mui/icons-material/Remove";
import AddIcon from "@mui/icons-material/Add";

type Props = {
  value: number | string;
  onRemoveClick: VoidFunction;
  onAddClick: VoidFunction;
}

export const ProductCount = memo((props: Props) => {
  return (
    <Box display="flex" gap="5px" alignItems="center">
      <IconButton size="small" color="default" aria-label="-" onClick={props.onRemoveClick}>
        <RemoveIcon fontSize="small" />
      </IconButton>
      <Typography sx={{ textAlign: "center" }} id="modal-modal-title" variant="body2">
        {props.value}
      </Typography>
      <IconButton size="small" color="default" aria-label="+" onClick={props.onAddClick}>
        <AddIcon fontSize="small" />
      </IconButton>
    </Box>
  );
});