import { observer } from "mobx-react-lite";
import Box from '@mui/material/Box';
import * as Components from "./components";
import { useSwipe, useList } from "./app.hooks";

export const App = observer(() => {
  const { handleTouchStart, handleTouchMove, handleTouchEnd } = useSwipe();
  const list = useList();

  return (
    <Box sx={{ maxWidth: "100%", overflow: "hidden", position: "relative" }} onTouchStart={handleTouchStart} onTouchMove={handleTouchMove} onTouchEnd={handleTouchEnd}>
      <Components.Categories />
      {list}
      <Components.OrderButton />
      <Components.AddressModal />
      <Components.ProductDrawer />
      <Components.OrderDetails />
    </Box>
  );
});
